html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* 가상 Dom 전역 수정 여기서부터 */
@import url("https://fonts.googleapis.com/css?family=Do+Hyeon&display=swap&subset=korean");

html,
body {
  height: 100%;
}
#root {
  height: 100%;
  font-family: "Do Hyeon";
}
#kakao-link-btn {
  display: none;
}

h1 {
  font-size: 6vmax;
}
h2 {
  font-size: 3.5vmax;
}
h3 {
  font-size: 3.2vmax;
}
h4 {
  font-size: 1.7vmax;
}
h6 {
  font-size: 1.5vmax;
}

@media (max-width: 450px) {
  h1 {
    font-size: 8.5vmax;
  }
  h2 {
    font-size: 7vmax;
  }
  h3 {
    font-size: 4.5vmax;
  }
  h4 {
    font-size: 3.5vmax;
  }
  h6 {
    font-size: 2.3vmax;
  }
}
