@import url(https://fonts.googleapis.com/css?family=Do+Hyeon&display=swap&subset=korean);
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* 가상 Dom 전역 수정 여기서부터 */

html,
body {
  height: 100%;
}
#root {
  height: 100%;
  font-family: "Do Hyeon";
}
#kakao-link-btn {
  display: none;
}

h1 {
  font-size: 6vmax;
}
h2 {
  font-size: 3.5vmax;
}
h3 {
  font-size: 3.2vmax;
}
h4 {
  font-size: 1.7vmax;
}
h6 {
  font-size: 1.5vmax;
}

@media (max-width: 450px) {
  h1 {
    font-size: 8.5vmax;
  }
  h2 {
    font-size: 7vmax;
  }
  h3 {
    font-size: 4.5vmax;
  }
  h4 {
    font-size: 3.5vmax;
  }
  h6 {
    font-size: 2.3vmax;
  }
}

.FirstScreen {
  position: fixed;
  width: 100vw;
  height: 100%;

  -webkit-animation: fadein;

          animation: fadein;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
}
@-webkit-keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.Desktop {
  display: flex;
  height: 100%;
}
.Desktop_section1 {
  width: 40vw;
  height: 100%;
  margin-left: 15%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Desktop_picture {
  max-width: 100%;
  max-height: 100%;
}

.Desktop_section2 {
  width: 60vw;
  height: 100%;
  padding: 5%;
}
.Desktop_section2_header {
  height: 15%;

  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-around;
}

.Desktop_section2_body {
  height: 60%;

  padding-top: 15%;
  padding-bottom: 5%;
  padding-left: 20%;
  padding-right: 20%;
}
.Desktop_body_title {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.Desktop_body_title_description1 {
  display: flex;
  align-items: flex-end;
}
.Desktop_body_title_description2 {
  display: flex;
  justify-content: flex-end;
}
.Desktop_body_title_description3 {
  display: flex;
  justify-content: center;
  color: #dc3545;

  -webkit-animation: fadein2 1.3s ease infinite alternate;

          animation: fadein2 1.3s ease infinite alternate;
}
@-webkit-keyframes fadein2 {
  0% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadein2 {
  0% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}

.Desktop_section2_footer {
  height: 25%;

  padding-left: 25%;
  padding-right: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Desktop_button {
  height: 10vh;
  border-radius: 1rem;
  font-size: 1.5vmax;
}

/* 데스크탑 환경 초기화 */
@media (min-width: 450px) {
  .FirstScreen_header {
    display: none;
  }
  .FirstScreen_body {
    display: none;
  }
  .FirstScreen_footer {
    display: none;
  }
}
@media (max-width: 450px) {
  /* 모바일 환경 초기화 */
  .Desktop {
    display: none;
  }

  .FirstScreen_header {
    height: 12%;
    padding-left: 5%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .FirstScreen_body {
    height: 67.5%;
    padding-top: 5%;
    padding-left: 5%;
    padding-right: 5%;
  }
  .FirstScreen_body_background {
    height: 100%;
  }
  .FirstScreen_body_title {
    height: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  .FirstScreen_body_title_inline {
    display: flex;
    align-items: flex-end;
  }
  .FirstScreen_body_picture {
    height: 70%;
    margin-left: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .FirstScreen_picture {
    max-width: 100%;
    max-height: 100%;
  }

  .FirstScreen_footer {
    height: 20.5%;
    padding-left: 5vw;
    padding-right: 5vw;
    display: flex;
    align-items: center;
  }
  .FirstScreen_button_start {
    height: 12vh;
    border-radius: 1rem;
    font-size: 4vmax;
  }
}

.LoadingScreen {
  position: fixed;
  width: 100vw;
  height: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.loading {
  margin-bottom: 30px;
}

.MainLayout {
  height: 87%;
}
.MainLayout_foodSection {
  height: 60%;
  padding-top: 5%;
  padding-left: 5%;
  padding-right: 5%;
}
.MainLayout_foodPicture {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.MainLayout_picture {
  max-width: 100%;
  max-height: 100%;
}

.MainLayout_mainMenuSection {
  height: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.MainLayout_descriptionSection {
  height: 30%;
  padding-left: 5%;
  padding-right: 5%;
}

.MainLayout_description {
  height: 100%;
  padding-top: 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 3px solid black;
  border-radius: 1.5rem;
}
.MainLayout_description2 {
  height: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.MainLayout_foodName {
  height: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #dc3545;
}

